
import "./Loader.css";

export function LoaderChange() {
    return (
        <>
        <div className="bodyDiv">
            <div className="rhombus">
                <div className="circle1"></div>
                <div className="circle2"></div>
            </div>
            <div className="text">
                <p>Loading...</p>
            </div>
            </div>
        </>
    );
}


export function LoaderPump() {
    return (
        <>
        <div className="bodyDiv2">
            <div className="rhombus2">
                <div className="circle21"></div>
                <div className="circle22"></div>
            </div>
            <div className="text">
                <p>Loading...</p>
            </div>
            </div>
        </>
    );
}
