import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Modal from "react-bootstrap/Modal";
import Pagination from "react-bootstrap/Pagination";
import text_vdo from "../../videos/text_vdo.mp4";

import "../style.css";

import { Image } from "react-bootstrap";
import { useState, useRef, useEffect } from "react";
import { LoaderChange, LoaderPump } from "../Loader/Loader";

import ImageList from "@mui/material/ImageList";
import ImageListItem from "@mui/material/ImageListItem";

import { local_images } from "../../constant/Constant";

function Home() {
  const [modalShow, setModalShow] = useState({
    id: 1,
    url: local_images[0].url,
    view: false,
  });

  const setPrevNextImg = (id) => {
    setModelLoading(true);
    if (id !== 0) {
      const prevNextImg = local_images.filter((item) => {
        return item.id === id;
      });
      if (prevNextImg.length !== 0) {
        setModalShow({
          id: prevNextImg[0].id,
          url: prevNextImg[0].url,
          vdoUrl: prevNextImg[0].vdoUrl,
          view: true,
        });
      } else {
        setModalShow({
          id: 1,
          url: local_images[0].url,
          vdoUrl: null,
          view: true,
        });
      }
    } else {
      setModalShow({
        id: local_images[local_images.length - 1].id,
        url: local_images[local_images.length - 1].url,
        vdoUrl: local_images[local_images.length - 1].vdoUrl,
        view: true,
      });
    }
  };

  const [loading, setLoading] = useState(true);
  const counter = useRef(0);
  const imageLoaded = () => {
    counter.current += 1;
    if (counter.current >= local_images.length) {
      setLoading(false);
    }
  };

  const [modelLoading, setModelLoading] = useState(true);
  useEffect(() => {
    if (modalShow.view) {
      setModelLoading(true);
    }
  }, [modalShow.view]);
  const modelImageLoaded = () => {
    setModelLoading(false);
  };

  function MyVerticallyCenteredModal(props) {
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">Art</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {modelLoading && (
            <div className="modelLoader">
              <LoaderChange />
            </div>
          )}
          <div className={modelLoading ? "modalDiv hideDiv" : "modalDiv"}>
            {modalShow.vdoUrl ? (
              <video
                className="modelImgVdo"
                autoPlay={true}
                loop={true}
                onPlay={modelImageLoaded}
              >
                <source src={modalShow.vdoUrl} type="video/mp4" />
              </video>
            ) : (
              <Image
                className="modelImgVdo"
                src={modalShow.url}
                rounded
                fluid
                onLoad={modelImageLoaded}
              />
            )}
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Pagination>
            <Pagination.Prev onClick={() => setPrevNextImg(modalShow.id - 1)} />
            <Pagination.Item>{modalShow.id}</Pagination.Item>
            <Pagination.Next onClick={() => setPrevNextImg(modalShow.id + 1)} />
          </Pagination>
        </Modal.Footer>
      </Modal>
    );
  }

  return (
    <div className="homeBody">
      {loading && <LoaderPump />}
      <Container className={loading && "hideDiv"}>
        <br></br>
        <br></br>
        <br></br>
        <div className="texdVdoDiv">
          <video className="texdVdo" autoPlay={true} loop={true} muted={true}>
            <source src={text_vdo} type="video/mp4" />
          </video>
        </div>
        <Row>
          <MyVerticallyCenteredModal
            show={modalShow.view}
            onHide={() =>
              setModalShow({
                id: 1,
                url: local_images[0].url,
                vdoUrl: null,
                view: false,
              })
            }
          />
        </Row>
        <Row>
          <ImageList cols={3} gap={8}>
            {local_images.map((img) => (
              <ImageListItem key={img.id}>
                <img
                  onClick={() =>
                    setModalShow({
                      id: img.id,
                      url: img.url,
                      vdoUrl: img.vdoUrl,
                      view: true,
                    })
                  }
                  src={`${img.url}?w=248&fit=crop&auto=format`}
                  srcSet={`${img.url}?w=248&fit=crop&auto=format&dpr=2 2x`}
                  alt={img.id}
                  // loading="lazy"
                  onLoad={imageLoaded}
                />
              </ImageListItem>
            ))}
          </ImageList>
        </Row>
      </Container>
    </div>
  );
}

export default Home;
